<template>
	<div>
		<h1>Commentaires Test</h1>
		<div>
			<table style="border:none;cellspacing:0;">
				<tbody>
					<tr v-for="commentaire in commentaires.commentaires" style="height:80px;">
						<td v-if="commentaire.affichage.gras === 'false'" width="2%" style="background-color:	rgb(194, 194, 214)"></td>
						<td v-if="commentaire.affichage.gras === 'true'" width="2%" style="background-color:rgb(102, 153, 115)"></td>
						<td width="18%" style="background-color:rgb(224, 224, 235);position:relative;">
							<div class="minute">{{commentaire.minute}}</div>
							<div class="type">
								<img v-if="commentaire.type === 'but'"src="/static/img/commentairesIcons/ball.png" style="width:20px;height:20px;">
								<img v-if="commentaire.type === 'rouge'"src="/static/img/commentairesIcons/rouge.png" style="width:20px;height:20px;">
								<img v-if="commentaire.type === 'jaune'"src="/static/img/commentairesIcons/jaune.png" style="width:20px;height:20px;">
								<img v-if="commentaire.type === 'change'"src="/static/img/commentairesIcons/change.png" style="width:20px;height:20px;">
							</div>
						</td>
						<td width="80%">
							<div class="text" v-if="commentaire.affichage.gras === 'true'"style="font-weight:bold;text-align:left;">{{commentaire.text}}</div>
							<div class="text" v-if="commentaire.affichage.gras === 'false'"style="font-weight:normal;text-align:left;">{{commentaire.text}}</div>
						</td>
					</tr>
				</tbody>
			</table>
		</div>
	</div>
</template>

<script>
var debounce = require('lodash.debounce')

export default {
  name: 'commentaires',

  data () {
    return {
      commentaires: [],
      commentaire: ''
    }
  },
  methods: {
    updateCommentaires: debounce(function () {
      if (this.$localStorage.get('online') === 'true' && this.$localStorage.get('id')) {
        this.$http.get('http://localhost:8080/static/data/commentaires' + this.$localStorage.get('id') + '.json').then(response => {
        // json success call back
          this.commentaires = response.data
          this.$localStorage.set('commentaires', JSON.stringify(response.data))
        }, response => {
          // error callback
          console.log('error call data')
        })
      } else {
        this.commentaires = JSON.parse(this.$localStorage.get('commentaires'))
      }
    }, 1000)
  },

  watch: {
    matches: function () {
      this.updateCommentaires()
    }
  },

  created: function () {
    this.updateCommentaires()
    window.addEventListener('online', this.updateCommentaires)
  }
}
</script>

<style>
body,html {
	margin: 0;
	width: 100%;
	height: 100%;
}
.commentaires{
	min-height: 80px;
}
.commentaire{
	display: flex;
	min-height: 100px;
	width: 100%;
	margin: 10px 0;
}
.left-border{
	background-color: rgb(194, 194, 214);
}
.minute{
	font-weight: bold;
	font-size: 20px;

	position: absolute; 
	width:100%;
	top:0;
	left:0;
	text-align: center;
}
.type{
	position: absolute;
	width:100%;
	top:30px;
	left:0;
	text-align: center;
}
.right{
	min-height: 80px;
	border-bottom: 1px dashed rgb(194, 194, 214);
}
.text{
	display: flex;
	padding: 10px 0;
	line-height: 120%;
	font-size: calc(12px + 0.6vw + 0.4vh);
}
td{border-bottom:1px solid rgb(224, 224, 235)}
</style>


